import React, { useEffect, useMemo, useState } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import { API_ENDPOINTS } from '../../config/api';
import PageTransitionLoader from '../../components/PageTransitionLoader';
import './styles/landing.scss';

const PaymentResult: React.FC = () => {
  const { search } = useLocation();
  const navigate = useNavigate();
  const query = useMemo(() => new URLSearchParams(search), [search]);

  const status = query.get('status'); // 'success' or 'fail'
  const message = query.get('message') || '';
  const oid = query.get('oid') || '';

  const [email, setEmail] = useState('');
  const [isSent, setIsSent] = useState(false);
  const [loading, setLoading] = useState(false);
  const [isPolling, setIsPolling] = useState(false);

  //    
  useEffect(() => {
    if (status === 'success' && oid) {
      setIsPolling(true);
      
      const pollReport = async () => {
        try {
          const response = await fetch(API_ENDPOINTS.GET_REPORT(oid));
          const data = await response.json();
          
          if (data.success && data.report_url) {
            console.log('    -> ');
            setIsPolling(false);
            navigate(`/disease-report?oid=${oid}`);
            return true;
          }
          return false;
        } catch (err) {
          console.error('Polling error:', err);
          return false;
        }
      };

      //  1    
      pollReport();
      
      const intervalId = setInterval(async () => {
        const finished = await pollReport();
        if (finished) clearInterval(intervalId);
      }, 3000); // 3 

      return () => clearInterval(intervalId);
    }
  }, [status, oid, navigate]);

  const handleSendEmail = async () => {
    if (!email || !email.includes('@')) {
      alert('   .');
      return;
    }

    setLoading(true);
    try {
      const response = await fetch(API_ENDPOINTS.UPDATE_EMAIL, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ oid, email }),
      });

      const data = await response.json();
      if (data.success) {
        setIsSent(true);
        alert('   .');
      } else {
        alert(' : ' + data.error);
      }
    } catch (error) {
      console.error('Email update failed:', error);
      alert('   .');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="dp-landing">
      <PageTransitionLoader isVisible={isPolling} message="  ..." />
      <header className="dp-header">
        <div className="logo">(Xog)</div>
        <h1 className="title"> </h1>
      </header>

      <main className="dp-content">
        <section className="result-section">
          {isPolling ? (
            <div className="result-card loading">
              <div className="icon"></div>
              <h2>  ...</h2>
              <p className="description"> . AI     .</p>
            </div>
          ) : status === 'success' ? (
            <div className="result-card success">
              <div className="icon"></div>
              <h2> !</h2>
              <p className="description">
                AI   .<br />
                {isSent ? (
                  <strong style={{ color: '#2ecc71' }}>{email}   .</strong>
                ) : (
                  ' (PDF)    .'
                )}
              </p>

              {!isSent && (
                <div className="email-collection">
                  <p className="hint">     .</p>
                  <div className="email-input-group">
                    <input 
                      type="email" 
                      placeholder="example@email.com" 
                      value={email}
                      onChange={(e) => setEmail(e.target.value)}
                      disabled={loading}
                    />
                    <button onClick={handleSendEmail} disabled={loading || !email}>
                      {loading ? ' ...' : ' '}
                    </button>
                  </div>
                </div>
              )}

              <div className="order-info">
                <div className="info-item">
                  <span className="label"></span>
                  <span className="value">{oid}</span>
                </div>
              </div>
            </div>
          ) : (
            <div className="result-card fail">
              <div className="icon"></div>
              <h2> .</h2>
              <p className="description">{message || '     .'}</p>
              <button 
                className="retry-button" 
                onClick={() => window.location.href = window.location.pathname}
              >
                 
              </button>
            </div>
          )}
        </section>
      </main>

      <footer className="dp-footer">
        <button className="home-button" onClick={() => window.close()}>
          
        </button>
      </footer>
    </div>
  );
};

export default PaymentResult;
