  const [showPanel, setShowPanel] = useState(false); //   
  const [showAgeCardGlow, setShowAgeCardGlow] = useState(false); //    
  const [showAbnormalCardsGlow, setShowAbnormalCardsGlow] = useState(false); //    
  const [cancerLabelFilter, setCancerLabelFilter] = useState<'ALL' | 'NORMAL' | 'BOUNDARY' | 'ABNORMAL'>('ALL'); //  
  const [diseaseLabelFilter, setDiseaseLabelFilter] = useState<'ALL' | 'NORMAL' | 'BOUNDARY' | 'ABNORMAL'>('ALL'); //  
  const [cancerSliderIndex, setCancerSliderIndex] = useState(0); //    
  const [diseaseSliderIndex, setDiseaseSliderIndex] = useState(0); //    
